.projectsCont{
    list-style: none;
    padding-left: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1fr);
    grid-row-gap: 50px;
}

.title{
    font-size: 32px;
    font-family: Notable;
    color: #fff;
    padding-left: 20px;
    padding-top: 10px;
    z-index: 3;
    position: absolute;
}

.overlay{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0;
    opacity: 0;
    background-color: #00000075;
    display: flex;
    align-items: flex-end;
}

.overlayText{
    color: #fff;
    padding: 0 15px 30px 15px
}

.webstudio{
    background-image: url('../../../assets/images/webstudio.jpg');
    background-position: center;
    background-size: cover;
    height: 300px;
    max-width: 264px;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
}

.webstudio:hover .overlay,
.webstudio:focus .overlay {
    opacity: 1;
    transition: all 0.4s;
}

